
import { defineComponent } from "vue";

interface StringKeyObject {
  [key: string]: string;
}

export default defineComponent({
  data() {
    return {
      keywords: {
        for: "operator",
        while: "operator",
        var: "keyword",
        fn: "keyword",
        new: "keyword",
        class: "keyword",
        if: "operator",
        else: "operator",
        return: "keyword",
        "+": "operator",
        "=": "operator",
        "-": "operator",
        "*": "operator",
        "/": "operator",
        "<": "operator",
        ">": "operator",
        ";": "grey",
        "{": "operator",
        "}": "operator",
        "(": "keyword",
        ")": "keyword",
        "[": "value",
        "]": "value",
        ",": "grey",
        " ": "space",
      } as StringKeyObject,
    };
  },

  methods: {
    updateLine(num: number | undefined) {
      this.$emit("setActiveLine", num);
    },

    parseLine(text: string | undefined) {
      let index = 0;
      interface Token {
        key: number;
        text: string;
        className: string;
        index: number;
      }

      const res: Token[] = [];
      let currentWord = "";
      for (let i = 0; i < text!.length; i++) {
        index++;
        const char = text![i];

        // Make the whole line grey if it starts with a comment
        if (i < text!.length - 1) {
          const nextChar = text![i + 1];
          if (char === "/" && nextChar === "/") {
            res.push({
              key: i,
              text: text!.slice(i),
              className: "grey",
              index,
            });
          }
        }

        // End of the word
        if (
          char === " " ||
          char === ";" ||
          char === "(" ||
          char === ")" ||
          char === "+" ||
          char === "-" ||
          char === "*" ||
          char === "/" ||
          char === "{" ||
          char === "}" ||
          char === '"' ||
          char === "[" ||
          char === "]" ||
          char === ","
        ) {
          // Get className
          let className = "variable";

          // isNaN because if val is 0, it will think it's false if I just do "if (parseInt)"
          if (!isNaN(parseInt(currentWord))) className = "value";
          else if (this.keywords[currentWord]) className = this.keywords[currentWord];

          res.push({
            key: i,
            text: currentWord,
            className: className,
            index,
          });

          currentWord = "";

          // Add the operator
          res.push({
            key: i,
            text: char,
            className: this.keywords[char] || "variable",
            index,
          });
        } else currentWord += char; // Continue the word
      }

      if (currentWord.length > 0) {
        res.push({
          key: text!.length,
          text: currentWord,
          className: this.keywords[currentWord] || "variable",
          index,
        });
      }

      return res;
    },
  },

  name: "ScriptLine",
  props: {
    text: String,
    activeLine: Boolean,
    lineNumber: { type: Number, required: true },
    setActiveLine: Function,
  },

  computed: {
    console: () => console,
  },
});
